<template>
  <div>
    <div class="column is-5">
      <fixture-team
        :teamId="fixture.homeTeamId"
        :teamName="fixture.homeTeamName"
        :teamCrest="fixture.homeTeamCrest">
      </fixture-team>
    </div>

    <div class="column is-2">
      <div class="card">
        <div class="card-content">
          <div class="content has-text-centered">
            <span v-text="fixture.result.goalsHomeTeam"></span>
            -
            <span v-text="fixture.result.goalsAwayTeam"></span>
          </div>
          <div class="has-text-centered">
            (<span :title="fixture.date">{{ fixture.date | fromNow }}</span>)
          </div>
        </div>
      </div>
    </div>

    <div class="column is-5">
      <fixture-team
        :teamId="fixture.awayTeamId"
        :teamName="fixture.awayTeamName"
        :teamCrest="fixture.awayTeamCrest">
      </fixture-team>
    </div>
  </div>
</template>
<script>
import DateParsers from '@/mixins/DateParsers';
import FixtureTeam from './FixtureTeam';

export default {
  name: 'Fixture',
  props: {
    fixture: {
      required: true,
      type: Object,
    },
  },
  mixins: [DateParsers],
  components: {
    FixtureTeam,
  },
  data() {
    return {
      searchTeam: '',
    };
  },
  computed: {
    searchHits() {
      return this.filteredFixtures.length;
    },
    fixtures() {
      return this.$store.getters.getLeagueFixturesByStatus(this.status);
    },
    filteredFixtures() {
      if (!this.searchTeam.length) {
        return this.fixtures;
      }
      return this.fixtures.filter((fixture) => {
        if (
          fixture.awayTeamName.toLowerCase().includes(this.searchTeam.toLowerCase()) ||
          fixture.homeTeamName.toLowerCase().includes(this.searchTeam.toLowerCase())
        ) {
          return fixture;
        }
      });
    },
  },
};
</script>



