<template>
  <div class="column is-10">
    <div class="field">
      <div class="control has-icons-left has-icons-right">
        <input class="input" type="text" placeholder="Search..." v-model="searchTeam"/>
        <span class="icon is-small is-left">
          <i class="fas fa-search"></i>
        </span>
        <span class="icon is-small is-right">
          <i v-text="searchHits"></i>
        </span>
      </div>
    </div>

    <fixture
      class="columns"
      :fixture="fixture"
      v-for="fixture in filteredFixtures"
      :key="fixture.date+fixture.homeTeamName"
    >
    </fixture>
  </div>
</template>
<script>
import DateParsers from '@/mixins/DateParsers';
import Fixture from './Fixture';

export default {
  name: 'Fixtures',
  props: {
    status: {
      required: false,
      type: String,
      default: 'ALL',
    },
  },
  mixins: [DateParsers],
  components: {
    Fixture,
  },
  data() {
    return {
      searchTeam: '',
    };
  },
  computed: {
    searchHits() {
      return this.filteredFixtures.length;
    },
    fixtures() {
      return this.$store.getters.getLeagueFixturesByStatus(this.status);
    },
    filteredFixtures() {
      if (!this.searchTeam.length) {
        return this.fixtures;
      }
      return this.fixtures.filter((fixture) => {
        if (
          fixture.awayTeamName.toLowerCase().includes(this.searchTeam.toLowerCase()) ||
          fixture.homeTeamName.toLowerCase().includes(this.searchTeam.toLowerCase())
        ) {
          return fixture;
        }
      });
    },
  },
};
</script>
<style scoped>
  .columns { border-bottom: 2px solid #cecece; }
</style>



