<template>
  <div class="card">
    <div class="card-content">
      <div class="media">
        <div class="media-content">
          <div class="has-text-centered">
            <figure class="image is-squared is-32x32" style="margin: 0 auto">
              <img :alt="teamName" :src="teamCrest" aria-hidden="true">
            </figure>
            <br>
            <router-link :to="{ name: 'Team', params: { id: teamId }}" class="title is-5" v-text="teamName"></router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'FixtureTeam',
  props: {
    teamCrest: {
      required: false,
      default: 'https://bulma.io/images/placeholders/96x96.png',
    },
    teamId: {
      required: false,
    },
    teamName: {
      required: true,
    },
  },
};
</script>



